/* form Returning Player and New Player*/


.container{background:white; border-radius: 10px; -webkit-box-shadow:15px 20px 0px rgba(0,0,0,0.1); box-shadow:15px 20px 0px rgba(0,0,0,0.1);}
.login-sec{padding: 30px 30px; position:relative;}
.login-sec .copy-text{position:absolute; width:80%; bottom:20px; font-size:13px; text-align:center;}
.login-sec .copy-text i{color: #81CFDF;}
.login-sec .copy-text a{color: #81CFDF;}
.login-sec h2{margin-bottom:30px; font-weight:800; font-size:30px; color: black}
.login-sec h2:after{content:" "; width:100px; height:5px; background:black; display:block; margin-top:20px; border-radius:3px; margin-left:auto;margin-right:auto}
.btn-login{background:  #4FA887; color:#fff; font-weight:600;}

.btn-login:hover {
	opacity: 1;
   
   /*Reflection*/
  -webkit-box-reflect: below 0px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(.7, transparent), to(rgba(0,0,0,0.4)));
 
   /*Glow*/
  -webkit-box-shadow: 0px 0px 20px rgba(255,255,255,1);
  box-shadow: 0px 0px 20px rgba(255,255,255,1);
}


